.color-control {
	.components-base-control__field,
	.components-base-control {
		margin: 0 !important;
	}

	.color-label label {
		margin: 0;
	}

	.color-indicator {
		position: relative;
		width: 25px;
		height: 25px;
		border: none;
		background: none;
		cursor: pointer;
	}

	.component-color-indicator {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: linear-gradient(45deg, #ddd 25%, transparent 0),
			linear-gradient(-45deg, #ddd 25%, transparent 0),
			linear-gradient(45deg, transparent 75%, #ddd 0),
			linear-gradient(-45deg, transparent 75%, #ddd 0);
		background-position:
			0 0,
			0 5px,
			5px -5px,
			-5px 0;
		background-size: 10px 10px;
	}
}

// colors palette
.color-indicator {
	position: relative;
	display: block;
	width: 25px;
	height: 25px;
	cursor: pointer;
}

div#colors-palette {
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	justify-content: flex-start;
	gap: 5px;
}

.colors-palette {
	padding: 0 15px 15px;
}

.color-indicator.active::after {
	position: absolute;
	top: 50%;
	left: 50%;
	display: flex;
	width: 15px;
	height: 15px;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	background: black;
	color: #fff;
	content: "âœ“";
	font-size: 10px;
	transform: translate(-50%, -50%);
}

// reset button
.reset-button {
	width: 12px;
	min-width: unset !important;
	padding: 0 !important;
	margin: 0 !important;
	color: #007cba;

	&:focus {
		box-shadow: none !important;
		outline: none !important;
	}

	&.disabled {
		cursor: not-allowed;

		.dashicon {
			color: #e0e0e0;
		}
	}

	&.active {
		.dashicon {
			color: #007cba;
		}
	}

	.dashicon {
		font-size: 12px;
		line-height: 20px;
	}
}
